@import url("https://fonts.googleapis.com/icon?family=Material+Icons");
@import url("https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap");

.posts::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

.story {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1em .5em;
  }
.storyBorder {
    padding: 2px;
    background: linear-gradient(200deg, #da3394 25%, #e03c67 50%, #f3753b 75%, #f99b4a);
    border-radius: 50%;
}

.profileIcon.big {
    width: 60px;
    height: 60px;
}

.profileIcon {
    border-radius: 50%;
    border: 2px solid #fff;
    display: block;
}

body {
    font-family: "roboto";
    font-size: 16px;
    background-color: whitesmoke;
    padding: 0;
    margin: 0;
}

.app{
    padding: 20px;
    
}
.MuiBox-root{
    background-color: white;
}
.story .accountName {
    font-size: .65em;
    margin: .4em;
  }